.post {
  @include color-style($bgc: $theme-grey);
  overflow-x: hidden;
  padding: 18px 10px 18px 17px;
  margin-top: 0;
  margin-bottom: 20px;

  @media (min-width: $tablet-breakpoint) {
    display: grid;
    grid-template-rows: min-content 1fr min-content;
    padding: 23px 24px 21px 30px;
    min-height: 111px;
  }

  @media (min-width: $desktop-breakpoint) {
    padding: 24px 24px 20px 28px;
    margin-bottom: 54px;
  }

  &--panorama {
    @media (min-width: $tablet-breakpoint) {
      grid-template-columns: 1fr 124px;
      grid-template-rows: min-content min-content;
      min-height: 50px;
      margin-bottom: 50px;
      padding: 23px 24px 15px 30px;

      br {
        display: none;
      }
    }

    @media (min-width: $desktop-breakpoint) {
      padding: 24px 24px 2px 28px;
    }
  }
}

.post__signature {
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-breakpoint) {
    &--panorama {
      grid-column: 1 / 2;
    }
  }

  .post__author {
    @include font-style($weight: 700);
  }

  .post__time {
    @include font-style(12px, 24px, 300);
    text-transform: uppercase;
  }
}

.post__text {
  margin-top: 0;
  margin-bottom: 10px;

  @media (min-width: $tablet-breakpoint) {
    overflow: hidden;
    min-height: 35px;


    &--panorama {
      grid-column: 1 / 3;
      margin-bottom: 10px;
    }
  }
}

